<template>
  <div class="my-listen-history-container">
    <div class="album-box">
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="getRecentHeardList"
      >
        <div
          v-for="album in recentHeardList"
          :key="album._id"
        >
          <div @click="clickAlbum(album)" class="content-area">
            <BaseListLayout
              :album="album"
            />
          </div>
          <div class="split-line-1" />
        </div>
      </van-list>
    </div>
  </div>
</template>

<script>
import BaseListLayout from '@/components/homePageLayout/BaseListLayout.vue';
import { apiAlbumPlayList } from '@/service/api/program';
import { toAlbumPage } from '@/utils/proxyRouteSkip';
import Vue from 'vue';
import { Toast, List } from 'vant';

Vue.use(Toast).use(List);

export default {
  components: {
    BaseListLayout
  },

  data() {
    return {
      recentHeardList: [],
      pageSize: 20,
      pageNum: 0,
      loading: false,
      finished: true
    }
  },
  
  created() {
    this.setDocumentTitle();
  },

  mounted() {
    this.getRecentHeardList();
  },

  methods: {
    // todo 设置文档标题
    setDocumentTitle() {
      const receiveTitle = this.$route.query.hasOwnProperty('docTitle') ? decodeURIComponent( this.$route.query.docTitle ) : '';
      document.title = receiveTitle ? receiveTitle : '收听历史';
    },

    async getRecentHeardList() {
      const recentHeardResponse = await apiAlbumPlayList(this.pageNum, this.pageSize);
      if (!recentHeardResponse || recentHeardResponse.type === 'fail') return Toast( '服务器错误，请联系客服' );
      this.recentHeardList = this.recentHeardList.concat( recentHeardResponse.data );

      if (recentHeardResponse.data.length < this.pageSize || recentHeardResponse.data.length === 0) {
        this.finished = true;
      } else {
        this.pageNum += 1;
        this.finished = false;
      }

      this.loading = false;
    },

    clickAlbum(album) {
      toAlbumPage(album.chargeType, album.isBuy, album._id);
    }
  }
}
</script>

<style lang="scss" scoped>
.my-listen-history-container {
  width: 100%;
  background: #fff;

  .album-box {
    width: 100%;
    padding: 0 16px;
    box-sizing: border-box;

    .content-area {
      padding: 20px 0;
    }
  }

  .split-line-1 {
    width: 343px;
    height: 1px;
    background: rgba(233,233,233,1);
  }
}
</style>


